import React from 'react';
import { ListGroup } from 'react-bootstrap';
import _ from 'lodash';
import SearchResultsItem from './search-results-item/search-results-item.js';

const getSorter = (sortAscending, sortDescending) => {
  if (sortAscending) {
    return (item) => item.StarRating;
  }

  if (sortDescending) {
    return (item) => item.StarRating * -1;
  }

  return (items) => 0;
}


const getFilterPredicate = (filters) => (item) => 
  (_(filters).length === 0 || 
  _.intersection(filters, item.Facilities).length >= filters.length);

const SearchResults = props => {
  let filterPredicate = getFilterPredicate(props.facilities);
  let sorter = getSorter(props.sortAscending, props.sortDescending);
  let items = _(props.resultItems).filter(filterPredicate).sortBy(sorter).value();
  
  return (
    <ListGroup> 
    { items.map((item, i) =>
      <SearchResultsItem key={i} {...item} />) }
    </ListGroup>);
};

export default SearchResults;


