import React from 'react';
import { Grid, Navbar, Jumbotron, Row, Col } from 'react-bootstrap';

import SearchResults from '../search-results/search-results';
import FilterForm from '../facilities-filter/facilities-filter-form/facilities-filter-form';
import Sorting from '../sorting/sorting';
import { isSortedAscending, isSortedDescending } from '../sorting/sorting.reducer';
import { getActiveFilters } from '../facilities-filter/facilities-filter.reducer';

const Layout = props => (
  <div>
    <Navbar inverse fixedTop>
      <Grid>
        <Navbar.Header>
          <Navbar.Brand>
            <a href="/">React Kata</a>
          </Navbar.Brand>
          <Navbar.Toggle />
        </Navbar.Header>
      </Grid>
    </Navbar>
    <Jumbotron>
      <Grid>
        <h1>Hotel Search</h1>
        <Row>
          <Col md={6}>
            <Sorting dispatch={props.dispatch} sorting={props.sorting} />
          </Col>
        </Row>
        <Row>
          <Col md={6}>
            <FilterForm filters={props.filters} dispatch={props.dispatch} />
          </Col>
        </Row>
        <Row>
          <Col md={6}>
            <SearchResults 
              resultItems={props.items} 
              sortAscending={isSortedAscending(props.sorting)} 
              sortDescending={isSortedDescending(props.sorting)} 
              facilities={getActiveFilters(props.filters)} />
          </Col>
        </Row>
      </Grid>
    </Jumbotron>
  </div>);

export default Layout;



