import React from 'react';
import _ from 'lodash';
import { ToggleButtonGroup, ToggleButton, Form, FormGroup, Col, ControlLabel } from 'react-bootstrap';
import { toggleSortingDescending, toggleSortingAscending } from './sorting.actions';
import { isSortedAscending, isSortedDescending } from './sorting.reducer';

export const styles = {
  sorting: 'sorting',
  sortingButtonDescending: 'sorting-descending',
  sortingButtonAscending: 'sorting-ascending'
};

const Sorting = (props) => {
  const enabledValues = 
    isSortedDescending(props.sorting) ? [0] : 
    isSortedAscending(props.sorting) ? [1] : [];

  const onChange = (newEnabledValues) => {
    const newWithOld = _(newEnabledValues).difference(enabledValues);
    const oldWithNew = _(enabledValues).difference(newEnabledValues).value();
    const differences = newWithOld.concat(oldWithNew).value();

    if (_(differences).includes(0)) {
      props.dispatch(toggleSortingDescending);
    }

    if (_(differences).includes(1)) {
      props.dispatch(toggleSortingAscending);
    }
  }

  return (
    <Form horizontal>
      <FormGroup>
        <Col componentClass={ControlLabel} sm={4}>
          Sort by Rating
        </Col>
        <Col sm={8}>
          <ToggleButtonGroup
            type="checkbox"
            value={enabledValues}
            onChange={onChange}
            className={styles.sorting}
            bsClass={'small'}>
            <ToggleButton 
              className={styles.sortingButtonDescending}
              value={0}>Descending</ToggleButton>
            <ToggleButton 
              className={styles.sortingButtonAscending}
              value={1}>Ascending</ToggleButton>
          </ToggleButtonGroup>
        </Col>
      </FormGroup>
    </Form>)
}

export default Sorting;


