import { types } from './sorting.actions';

export const sortType = {
  ascending: 'ascending',
  descending: 'descending',
  none: ''
};

export const defaultState = sortType.none;

export const isSortedAscending = (state) => state === sortType.ascending;
export const isSortedDescending = (state) => state === sortType.descending;

const reducer = (state = defaultState, action) => {
  switch (action.type) {
    case types.toggleSortingDescending: {
      if (state === sortType.descending) {
        return sortType.none;
      }
      else {
        return sortType.descending;
      }
    }
    case types.toggleSortingAscending: {
      if (state === sortType.ascending) {
        return sortType.none;
      }
      else {
        return sortType.ascending;
      }
    }
    default:
      return state;
  }
}

export default reducer;


